/* CSS for the homepage */
.homepage-container {
    padding: 20px;
  }
  
  .about-section,
  .services-section {
    margin-bottom: 40px;
  }
  
  .about-content {
    text-align: center;
  }
  
  .about-content h2 {
    font-size: 28px;
    margin-bottom: 20px;
  }
  
  .about-content p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 30px;
  }
  
  .learn-more-button {
    padding: 12px 20px;
    background-color: #3498db;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .learn-more-button:hover {
    background-color: #2980b9;
  }
  
  .services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
  }
  
  .service-card {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .service-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .service-card i {
    font-size: 36px;
    margin-bottom: 10px;
  }
  
  .service-card h3 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .service-card p {
    font-size: 16px;
    line-height: 1.6;
  }
  
  .footer-section {
    text-align: center;
    padding: 20px 0;
    background-color: #f9f9f9;
    color: #333;
  }
  
  .services-heading {
    text-align: center;
    margin-bottom: 30px;
  }

  .dark-mode {
    background-color: black;
    color: white;
  }